import { MpFlex } from '@mekari/pixel-flex';
import { MpIcon } from '@mekari/pixel-icon';
import { MpText } from '@mekari/pixel-text';
import { MpButton } from '@mekari/pixel-button';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { keyframes } from '@emotion/css';

/**
 * Exported airene button styles.
 */
const border = keyframes`
  0% {
    background-position: 0% 20%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 100%;
  }
`;

const baseStyles = ({
  isShowBadge
}) => ({
  display: "inline-flex",
  justifyContent: "center",
  alignItems: "center",
  position: "relative",
  backgroundColor: "white",
  color: "blue.400",
  borderRadius: "25px",
  fontSize: "md",
  fontWeight: "semibold",
  paddingLeft: "9px",
  paddingRight: isShowBadge ? "3" : "9px",
  overflow: "hidden",
  paddingTop: "7.5px",
  paddingBottom: "7.5px",
  height: "auto",
  zIndex: "1",
  transition: "color 0.3s ease, background-color 0.3s ease",
  _before: {
    content: `''`,
    position: 'absolute',
    top: '-1.5px',
    left: '-1.5px',
    right: '-1.5px',
    bottom: '-1.5px',
    borderRadius: '23px',
    background: 'vibrantPurple',
    backgroundSize: '200% 200%',
    animation: `${border} 4s linear infinite`,
    transition: 'background-color 0.3s ease',
    zIndex: '-1'
  },
  _after: {
    content: `''`,
    position: 'absolute',
    top: '1.5px',
    left: '1.5px',
    right: '1.5px',
    bottom: '1.5px',
    borderRadius: 'full',
    backgroundColor: 'white',
    zIndex: '-1'
  },
  _hover: {
    '::before': {
      background: 'linear-gradient(90deg, var(--colors-blue-400), var(--colors-blue-400), var(--colors-blue-400))'
    }
  },
  _disabled: {
    color: "gray.400",
    cursor: 'not-allowed',
    '::before': {
      background: "gray.50"
    },
    '::after': {
      background: "gray.50"
    }
  }
});

const useAireneButtonStyles = props => {
  return { ...baseStyles(props)
  };
};

const MpAireneButton = {
  name: 'MpAireneButton',
  mixins: [createStyledAttrsMixin('MpAireneButton')],
  props: {
    as: {
      type: [String, Object],
      default: 'button'
    },
    to: [String, Object],
    type: {
      type: [String],
      default: 'button'
    },
    isDisabled: {
      type: [Boolean],
      default: false
    },
    isShowBadge: {
      type: [Boolean],
      default: true
    },
    ariaLabel: [String],
    ariaLabelledBy: [String]
  },
  computed: {
    getAireneButtonStyles() {
      return useAireneButtonStyles({
        isShowBadge: this.isShowBadge
      });
    }

  },

  render(h) {
    return h(MpButton, {
      props: { ...this.$props,
        size: 'md',
        variant: 'unstyled'
      },
      attrs: { ...this.getAireneButtonStyles,
        ...this.computedAttrs,
        ...this.attrs$
      },
      on: this.$listeners
    }, [this.$slots.default, this.isShowBadge && h(MpFlex, {
      attrs: {
        backgroundColor: this.isDisabled ? 'gray.25' : 'blue.50',
        borderRadius: 'full',
        paddingRight: '1',
        paddingLeft: '0.5',
        paddingY: '0.5',
        height: '4',
        marginLeft: '1'
      }
    }, [h(MpIcon, {
      props: {
        name: 'airene-brand',
        variant: 'fill',
        color: this.isDisabled ? 'gray.400' : 'blue.700'
      },
      attrs: {
        width: '3',
        height: '3'
      }
    }), h(MpText, {
      attrs: {
        fontSize: 'xs',
        fontWeight: 'semibold',
        color: this.isDisabled ? 'gray.400' : 'blue.700'
      }
    }, 'AI')])]);
  }

};

export { MpAireneButton };
