import { createStyledAttrsMixin, useId } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';
import { MpFlex } from '@mekari/pixel-flex';
import { MpIcon } from '@mekari/pixel-icon';
import { css } from '@emotion/css';

const MpAireneChatInput = {
  name: 'MpAireneChatInput',
  mixins: [createStyledAttrsMixin('MpAireneChatInput')],
  inject: {
    $useFormControl: {
      default: null
    }
  },
  model: {
    prop: 'value',
    event: 'input'
  },
  props: {
    id: {
      type: String
    },
    value: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: 'Ask something...'
    },
    maxlength: {
      type: String
    },
    ariaLabel: {
      type: String
    },
    ariaLabelledBy: {
      type: String
    },
    isReadOnly: {
      type: Boolean,
      default: false
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isInvalid: {
      type: Boolean,
      default: false
    },
    isRequired: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      currentValue: this.value,
      height: 20,
      rounded: 'full',
      isShowModal: false,
      isFocused: false
    };
  },

  computed: {
    getFormControls() {
      if (!this.$useFormControl) {
        return {
          controlId: this.controlId,
          isReadOnly: this.isReadOnly,
          isDisabled: this.isDisabled,
          isInvalid: this.isInvalid,
          isRequired: this.isRequired
        };
      }

      return this.$useFormControl(this.$props);
    },

    getId() {
      return this.getFormControls.controlId || this.id || `airene-chat-input${useId()}`;
    },

    isActive() {
      return this.currentValue.length > 0;
    },

    hasChildren() {
      return this.$slots.default;
    },

    getInputStyle() {
      return {
        width: 'inherit',
        minHeight: '20px',
        maxHeight: '136px',
        height: `${this.height}px`,
        outline: 'none',
        resize: 'none',
        fontSize: '14px',
        fontStyle: 'normal',
        fontWeight: '400',
        lineHeight: '20px',
        caretColor: 'blue.400',
        _disabled: {
          color: 'gray.400',
          backgroundColor: 'gray.50',
          cursor: 'not-allowed'
        }
      };
    },

    borderColor() {
      if (this.getFormControls.isInvalid) {
        return 'red.400';
      }

      if (this.isFocused) {
        return 'blue.400';
      }

      return 'gray.100';
    }

  },

  mounted() {
    this.rounded = this.hasChildren ? 'xl' : 'full';
  },

  methods: {
    css,

    handleInputSelectStyle() {
      return this.css`
        &::-moz-selection {
          color: var(--colors-dark);
          background: var(--colors-ice-50);
        }

        &::selection {
          color: var(--colors-dark);
          background: var(--colors-ice-50);
        }
      `;
    },

    handleFocus(e) {
      this.isFocused = true;
      this.$emit('focus', e);
    },

    handleBlur(e) {
      this.isFocused = false;
      this.$emit('blur', e);
    },

    handleInput(e) {
      const val = e.target.value;
      this.currentValue = val;
      this.$emit('input', val, e);
      this.$emit('change', e); // need to set height to auto first,
      // to prevent wrong height when delete input

      this.height = 'auto';
      this.$nextTick(() => {
        this.height = e.target.scrollHeight;

        if (!this.hasChildren) {
          if (this.height >= 40) {
            this.rounded = 'xl';
          } else {
            this.rounded = 'full';
          }
        }
      });
    },

    handleClick() {
      if (this.isDisabled) return;
      this.$emit('click', this.currentValue, this.getId);
    },

    handleShowModal() {
      this.isShowModal = !this.isShowModal;
    },

    handleSelectTextAll(e) {
      const textLabel = this.currentValue;

      if (textLabel) {
        e.target.setSelectionRange(0, textLabel.length);
      }
    },

    handleKeydown(e) {
      this.$emit('keydown', e);
    }

  },

  render(h) {
    return h(MpFlex, {
      props: {
        direction: 'column'
      },
      attrs: {
        id: this.getId,
        'data-pixel-component': 'MpAireneChatInput',
        position: 'relative',
        width: 'full',
        gap: '1.5',
        ...this.splitProps.styleAttrs
      }
    }, [h(MpFlex, {
      props: {
        direction: 'column'
      },
      attrs: {
        disabled: this.isDisabled || this.getFormControls.isDisabled,
        position: 'relative',
        width: 'inherit',
        gap: '2',
        pt: '2',
        pb: this.hasChildren ? '9' : '2',
        pl: '4',
        pr: '12',
        borderRadius: this.rounded,
        borderWidth: '1px',
        borderStyle: 'solid',
        borderColor: this.borderColor,
        transition: 'all 250ms',
        _hover: {
          borderColor: !this.isInvalid && !this.getFormControls.isInvalid && 'blue.400'
        },
        _disabled: {
          backgroundColor: 'gray.50',
          borderColor: 'gray.100',
          cursor: 'not-allowed'
        }
      }
    }, [h(MpBox, {
      props: {
        as: 'textarea'
      },
      domProps: {
        value: this.currentValue
      },
      attrs: {
        id: this.getId,
        placeholder: this.placeholder,
        maxlength: this.maxlength,
        autocomplete: 'off',
        rows: '1',
        readonly: this.isReadOnly || this.getFormControls.isReadOnly,
        disabled: this.isDisabled || this.getFormControls.isDisabled,
        required: this.isRequired || this.getFormControls.isRequired,
        'aria-readonly': this.isReadOnly || this.getFormControls.isReadOnly,
        'aria-disabled': this.isDisabled || this.getFormControls.isDisabled,
        'aria-invalid': this.isInvalid || this.getFormControls.isInvalid,
        'aria-label': this.ariaLabel,
        'aria-labelledby': this.ariaLabelledBy,
        ...this.getInputStyle,
        ...this.computedAttrs
      },
      class: [this.handleInputSelectStyle()],
      on: {
        focus: this.handleFocus,
        blur: this.handleBlur,
        input: this.handleInput,
        keydown: this.handleKeydown,
        click: this.handleSelectTextAll
      },
      style: {
        caretColor: 'var(--colors-blue-400)'
      }
    }), h(MpIcon, {
      props: {
        name: 'sent',
        color: this.isDisabled ? 'gray.100' : 'gray.600',
        variant: this.isActive ? 'duotone' : 'outline'
      },
      attrs: {
        cursor: this.isDisabled ? 'not-allowed' : 'pointer',
        position: 'absolute',
        bottom: '1.5',
        right: '4'
      },
      nativeOn: {
        click: this.handleClick
      }
    }), this.$slots.default && h(MpBox, {
      attrs: {
        position: 'absolute',
        bottom: '2',
        left: '4'
      }
    }, this.$slots.default)])]);
  }

};

export { MpAireneChatInput };
