import { MpBox } from '@mekari/pixel-box';
import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { keyframes } from '@emotion/css';

/**
 * Skeleton is used to show loading state of a component.
 *
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-skeleton
 */

const shimmerAnimation = keyframes`
  from {
    background-position: -100% 0;
  }

  to {
    background-position: 100% 0;
  }
`;
const MpSkeleton = {
  name: 'MpSkeleton',
  mixins: [createStyledAttrsMixin('MpSkeleton')],
  props: {
    variantColor: {
      type: String,
      default: 'gray'
    },
    duration: {
      type: String,
      default: '2s'
    }
  },
  computed: {
    getVariantColor() {
      const variantColor = {
        gray: 'linear-gradient(90deg, #d0d6dd, #edf0f2 50%, #d0d6dd)',
        blue: 'linear-gradient(90deg, #E0E9F7, #F5F9FF 50%, #E0E9F7)',
        purple: 'linear-gradient(90deg, #DCCFFF, #F3F0FD 50%, #DCCFFF)'
      };
      return variantColor[this.variantColor] || this.variantColor;
    }

  },

  render(h) {
    return h(MpBox, {
      class: this.className,
      props: {
        as: 'div'
      },
      attrs: {
        'data-pixel-component': 'MpSkeleton',
        animation: `${shimmerAnimation} ${this.duration} infinite reverse`,
        background: 'var(--shimmer-background)',
        bgSize: '200%',
        ...this.computedAttrs
      },
      style: {
        '--shimmer-background': this.getVariantColor
      }
    }, this.$slots.default);
  }

};

export { MpSkeleton };
